<template>
    <div>
         <vue-particles color="#fff" style="background:#24968b;height:100vh;position:fixed;width:100vw;"></vue-particles>

            <div class="row justify-content-center">
            <div class="col-md-8 p-3">
                <div class="shadow rounded-lg p-5 bg-white">
                    <p class="font-bold">JADWAL SHOLAT</p>
                    <v-select :options="lokasi" label="lokasi" v-model="lokasinya" :reduce="e => e.id"></v-select>
                    <!-- <select class='form-control' v-model="lokasinya">
                        <option v-for="(item, index) in lokasi" :key="index+'lokasi'" :value="item.id">{{item.lokasi}}</option>
                    </select> -->
                    <hr>
                    <div v-if="datanya">
                        <p class="text-center font-bold"> 
                            {{datanya.lokasi}}, 
                            {{datanya.daerah}}
                        </p>
                        <div v-for="(item, index) in td" :key="index+'jadwal'">
                            <div style="height:100px;" class="anim hover:bg-yellow-400 d-flex justify-content-center align-items-center shadow rounded-lg">
                                    <div class="font-bold uppercase text-2xl">
                                    {{cekantara}} {{item[cekantara]}}
                                    </div>
                            </div>
                            <p class="p-2 font-bold">
                                {{item.tanggal}}
                            </p>
                            <tr class="rounded-lg anim hover:bg-blue-400 shadow p-2">
                                <td class="p-2 capitalize font-bold" style="width:100vw;">imsak</td>
                                <td class="pl-1 pr-1">:</td>
                                <td class="p-2">{{item.imsak}}</td>
                            </tr>
                            <tr class="rounded-lg anim hover:bg-blue-400 shadow p-2">
                                <td class="p-2 capitalize font-bold" style="width:100vw;">subuh</td>
                                <td class="pl-1 pr-1">:</td>
                                <td class="p-2">{{item.subuh}}</td>
                            </tr>
                            <tr class="rounded-lg anim hover:bg-blue-400 shadow p-2">
                                <td class="p-2 capitalize font-bold" style="width:100vw;">terbit</td>
                                <td class="pl-1 pr-1">:</td>
                                <td class="p-2">{{item.terbit}}</td>
                            </tr>
                            <tr class="rounded-lg anim hover:bg-blue-400 shadow p-2">
                                <td class="p-2 capitalize font-bold" style="width:100vw;">dhuha</td>
                                <td class="pl-1 pr-1">:</td>
                                <td class="p-2">{{item.dhuha}}</td>
                            </tr>
                            <tr class="rounded-lg anim hover:bg-blue-400 shadow p-2">
                                <td class="p-2 capitalize font-bold" style="width:100vw;">dzuhur</td>
                                <td class="pl-1 pr-1">:</td>
                                <td class="p-2">{{item.dzuhur}}</td>
                            </tr>
                            <tr class="rounded-lg anim hover:bg-blue-400 shadow p-2">
                                <td class="p-2 capitalize font-bold" style="width:100vw;">ashar</td>
                                <td class="pl-1 pr-1">:</td>
                                <td class="p-2">{{item.ashar}}</td>
                            </tr>
                            <tr class="rounded-lg anim hover:bg-blue-400 shadow p-2">
                                <td class="p-2 capitalize font-bold" style="width:100vw;">maghrib</td>
                                <td class="pl-1 pr-1">:</td>
                                <td class="p-2">{{item.maghrib}}</td>
                            </tr>
                            <tr class="rounded-lg anim hover:bg-blue-400 shadow p-2">
                                <td class="p-2 capitalize font-bold" style="width:100vw;">isya</td>
                                <td class="pl-1 pr-1">:</td>
                                <td class="p-2">{{item.isya}}</td>
                            </tr>
                        
                        </div>
                    </div>
                </div>
            </div>
            
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import datefns from 'date-fns'
import listkota from '@/static/json/id_kota_sholat.json'
// import libur from '@/static/json/calendar_libur.json'
export default{
    data(){
        return{
            datanya:[],
            jadwal:[],
            vdata:{},
            lokasi:[],
            lokasinya:'',
            now:'',
            jam:'',
            cekantara:'subuh'
        }
    },
    mounted(){
        this.getData()
        if(localStorage.getItem('lokasinya')){
            this.lokasinya = localStorage.getItem('lokasinya');
        }else{
            this.lokasinya=this.$store.state.lokasinya
        }
        console.log('this.lokasi',this.lokasinya)
        this.$forceUpdate();
        // this.getLokasi();
        let tanggal = new Date();
        this.jam=parseInt(tanggal.toString().split(' ')[4].split(':')[0]);
        this.lokasi = listkota;
    },
    watch:{
        lokasinya(){
            this.getData()
        }
    },
    computed:{
        td(){
            let that =this;
            let data = this.jadwal;
            data=data.filter(e=>{
                if(e.date==that.now){
                    return e
                }
            })
            let cekantara='';
            let antara=data.filter(e=>{
                console.log(that.jam,parseInt(e.maghrib.split(':')[0]))
                console.log(that.jam,parseInt(e.isya.split(':')[0]))
                console.log(that.jam > parseInt(e.maghrib.split(':')[0]))
                console.log(that.jam < parseInt(e.isya.split(':')[0]))
                if(that.jam >= parseInt(e.subuh.split(':')[0]) && that.jam <= parseInt(e.dzuhur.split(':')[0])){
                    cekantara='dzuhur'
                    return e
                }else if(that.jam >= parseInt(e.dzuhur.split(':')[0]) && that.jam <= parseInt(e.ashar.split(':')[0])){
                    cekantara='ashar'
                    return e
                }else if(that.jam >= parseInt(e.ashar.split(':')[0]) && that.jam <= parseInt(e.maghrib.split(':')[0])){
                    cekantara='maghrib'
                    return e
                }else if(that.jam >= parseInt(e.maghrib.split(':')[0]) && that.jam <= parseInt(e.isya.split(':')[0])){
                    cekantara='isya'
                    return e
                }else{
                    cekantara='subuh'
                }
            })
            this.cekantara=cekantara
            console.log(cekantara)
            this.$forceUpdate()
            return data
        }
    },
    methods: {
        getData(){
            let tanggal = new Date();
            this.vdata.bulan = this.getTgl(tanggal.toString().split(' ')[1]);
            this.vdata.tahun = tanggal.toString().split(' ')[3];
            this.now=datefns.format(
                    new Date(),
                    "YYYY-MM-DD"
                )
            console.log(this.vdata.bulan)
            axios.get(`https://api.myquran.com/v1/sholat/jadwal/${this.lokasinya}/${this.vdata.tahun}/${this.vdata.bulan}`).then(res=>{
                // console.log(res)
                this.datanya=res.data.data;
                this.jadwal=res.data.data.jadwal;
                this.$forceUpdate()
            })
        },
        getLokasi(){
            axios.get('https://api.myquran.com/v1/sholat/kota/semua').then(res=>{
                this.lokasi = res.data
            })
        },
        getTgl(data){
            let hasil='';
            if(data=='Jan') {
                hasil=1;
            }
            else if(data=='Feb') {
                hasil=2;
            }
            else if(data=='Mar') {
                hasil=3;
            }
            else if(data=='Apr') {
                hasil=4;
            }
            else if(data=='May') {
                hasil=5;
            }
            else if(data=='Jun') {
                hasil=6;
            }
            else if(data=='Jul') {
                hasil=7;
            }
            else if(data=='Aug') {
                hasil=8;
            }
            else if(data=='Sep') {
                hasil=9;
            }
            else if(data=='Oct') {
                hasil=10;
            }
            else if(data=='Nov') {
                hasil=11;
            }
            else if(data=='Dec') {
                hasil=12;
            }
            return hasil;
        },
    },
    layout:'app_islamic'
}
</script>